<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>

      <!--  -->
      <div style="margin-top: 15px">
        <!-- guttercol -->
        <el-row :gutter="20">
          <!-- :spancol -->
          <el-col :span="8">
            <el-input clearable @clear="getUserList" placeholder="" v-model="queryInfo.query" class="input-with-select">
              <el-button @click="getUserList" slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible = true"></el-button>
          </el-col>

          <!--  -->
          <!-- addDialogVisible  -->
          <el-dialog
            title=""
            :visible.sync="addDialogVisible"
            width="50%"
            @close="addDialogClosed">
            <!--  -->
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
              <el-form-item label="" prop="username">
                <el-input v-model="addForm.username"></el-input>
              </el-form-item>
              <el-form-item label="" prop="password">
                <el-input v-model="addForm.password"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="addForm.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="mobile">
                <el-input v-model="addForm.mobile"></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false"> </el-button>
              <el-button type="primary" @click="adduser"> </el-button>
            </span>
          </el-dialog>

          <!--  -->
          <el-dialog
            title=""
            :visible.sync="editDialogVisible"
            width="50%"
            @close="editDialogClose"
            >
            <!--  -->
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
              <el-form-item label="">
                <el-input v-model="editForm.username" disabled></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="editForm.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="mobile">
                <el-input v-model="editForm.mobile"></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false"> </el-button>
              <el-button type="primary" @click="editUserInfo"> </el-button>
            </span>
          </el-dialog>

        </el-row>
      </div>

      <!--  -->
      <!-- :data prop -->
      <el-table border :data="userlist" stripe style="width: 100%">
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column label="">
          <!--  scope scope.row -->
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-button  type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
            <!--  -->
            <el-button @click="removeUserById(scope.row.id)"  type="danger" size="mini" icon="el-icon-delete"></el-button>
            <!--  -->
            <el-tooltip  :enterable="false" effect="dark" content="" placement="top">
              <el-button type="warning" size="mini" icon="el-icon-setting"  @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>

      </el-table>
      <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 2, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
  title=""
  :visible.sync="setRoledialogVisible"
  width="50%"
  @close="setRoleDialogClosed"
  >
  <div>
    <p>{{userInfo.username}} </p>
    <p>{{userInfo.role_name}} </p>
    <P>
      <el-select v-model="selectedRoleId" placeholder="">
        <el-option
          v-for="item in rolesList"
          :key="item.id"
          :label="item.roleName"
          :value="item.id">
        </el-option>
      </el-select>
    </P>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRoledialogVisible = false"> </el-button>
    <el-button type="primary" @click="saveRoleInfo"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    //  
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if (regEmail.test(value)) {
        // 
        return cb()
      }

      cb(new Error(''))
    }

    //  
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        // 
        return cb()
      }

      cb(new Error(''))
    }

    return {
      // 
      queryInfo: {
        //  
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3-10', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: '6-15', trigger: 'blur' }
        ],
        email: [
          { requried: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { requried: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },

      // 
      editDialogVisible: false,

      //  
      editForm: {},

      // 
      editFormRules: {
        email: [
          { requried: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { requried: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },

      // 
      setRoledialogVisible: false,

      // 
      userInfo: {},

      // 
      rolesList: [],

      // id
      selectedRoleId: ''

    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      // params 
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.userlist = res.data.users
      this.total = res.data.total
    },

    //  pagesize 
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },

    //  pagenum 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // switch 
    async userStateChanged (userinfo) {
      const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },

    //   @close
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },

    //   
    adduser () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('/users', this.addForm)
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },

    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // editForm
      this.editForm = res.data
      this.editDialogVisible = true
    },

    //  
    editDialogClose () {
      this.$refs.editFormRef.resetFields()
    },

    // 
    editUserInfo () {
      //  validate()
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        //  
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })

        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },

    // id
    async removeUserById (id) {
      //  
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        // icon
        type: 'warning'
      }).catch(err => {
        return err
      })

      //    confirm
      //    cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message('')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('!')
      this.getUserList()
    },

    // 
    async setRole (userInfo) {
      this.userInfo = userInfo

      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.rolesList = res.data

      this.setRoledialogVisible = true
    },

    //  
    async saveRoleInfo () {
      if (!this.selectedRoleId) {
        return this.$message.error('!')
      }

      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedRoleId
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getUserList()
      this.setRoledialogVisible = false
    },

    // 
    setRoleDialogClosed () {
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
</style>
