<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="warning"
        :closable="false"
        show-icon>
      </el-alert>

      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <!-- options  -->
          <!-- props   -->
          <!-- v-model  -->
          <!-- clearable  -->
           <el-cascader
            v-model="selectedCatKeys"
            :options="catelist"
            expand-trigger="hover"
            :props="cateProps"
            @change="handleChange"
            clearable
            >
            </el-cascader>
        </el-col>
      </el-row>

      <!-- tab  -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>

                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>

                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>

    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
      >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
      >
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      catelist: [],
      // 
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCatKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      // 
      addFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {}
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.catelist = res.data
    },

    // 
    handleChange () {
      //  getParamsData()  
      this.getParamsData()
    },

    //  tab 
    handleTabClick () {
      //  getParamsData()  
      this.getParamsData()
      console.log(this.activeName)
    },

    // 
    async getParamsData () {
      //  
      if (this.selectedCatKeys.length !== 3) {
        this.selectedCatKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // 
      // Id  
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: { sel: this.activeName }
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      res.data.forEach(item => {
        // attr_vals  ','  
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },

    //  
    addDialogClose () {
      this.$refs.addFormRef.resetFields()
    },

    // 
    addParams () {
      this.$refs.addFormRef.validate(async vaild => {
        if (!vaild) return
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },

    //  
    async showEditDialog (attrId) {
      // 
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${attrId}`, {
        params: { attr_sel: this.activeName }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    //  
    editDialogClose () {
      this.$refs.editFormRef.resetFields()
    },
    //  
    editParams () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
          attr_name: this.editForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        this.$message.success('')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },

    // id
    async removeParams (attrId) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // 
      if (confirmResult !== 'confirm') {
        return this.$message('')
      }

      // 
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${attrId}`)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getParamsData()
    },

    //  Enter 
    async handleInputConfirm (row) {
      //  
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      //  
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      // tag
      this.saveAttrVals(row)
    },

    //  attr_vals 
    async saveAttrVals (row) {
      // tag
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // attr_vals  ',' 
        attr_vals: row.attr_vals.join(',')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
    },

    //  
    showInput (row) {
      row.inputVisible = true
      // 
      // $nextTick 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    // 
    handleClose (i, row) {
      //  tag   attr_vals 
      row.attr_vals.splice(i, 1)
      //  
      this.saveAttrVals(row)
    }
  },
  // 
  computed: {
    //  true false
    isBtnDisabled () {
      if (this.selectedCatKeys.length !== 3) {
        return true
      }
      return false
    },
    // Id
    cateId () {
      if (this.selectedCatKeys.length === 3) {
        return this.selectedCatKeys[2]
      }
      return null
    },
    // 
    titleText () {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt{
  margin: 15px 0;
}
.el-tag{
  margin: 10px;
}
.input-new-tag{
  width: 150px;
}
.button-new-tag{
  margin-left: 10px;
}
</style>
