<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>

        <!--  -->
        <el-alert
          title="" center show-icon :closable="false" type="info">
        </el-alert>

        <!--  -->
        <!-- :active="activeIndex  -->
        <el-steps align-center :space="200" :active="activeIndex - 0" finish-status="success">
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>

        <el-form :model="goodslist" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="top">
          <!-- tab -->
          <!-- :tab-position="'left'" tab -->
          <el-tabs @tab-click="tabclicked" :before-leave="beforTabLeave" v-model="activeIndex" :tab-position="'left'">
            <el-tab-pane label="" name="0">
              <el-form-item label="" prop="goods_name">
                <el-input v-model="goodslist.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_price">
                <el-input v-model="goodslist.goods_price" type="number"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_weight">
                <el-input v-model="goodslist.goods_weight" type="number"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_number">
                <el-input v-model="goodslist.goods_number" type="number"></el-input>
              </el-form-item>
              <el-form-item label="" prop="goods_cat">
                <el-cascader
                v-model="goodslist.goods_cat"
                :options="catelist"
                expand-trigger="hover"
                :props="cateProps"
                @change="handleChange">
                </el-cascader>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="1">
              <!-- Item -->
              <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                <!--  -->
                 <el-checkbox-group v-model="item.attr_vals">
                  <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="2">
              <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                <el-input v-model="item.attr_vals"></el-input>
              </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="" name="3">
              <!-- action api -->
              <!-- on-preview -->
              <!-- on-remove  -->
              <!-- list-type  -->
              <!-- headers  -->
              <!-- on-success  -->
              <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess">
                <el-button size="small" type="primary"></el-button>
              </el-upload>
            </el-tab-pane>
            <el-tab-pane label="" name="4">
              <!--  -->
              <quill-editor v-model="goodslist.goods_introduce"></quill-editor>
              <!--  -->
              <el-button type="primary" class="btnAdd" ></el-button>
            </el-tab-pane>
          </el-tabs>
        </el-form>

    </el-card>

     <!--   -->
    <!-- <el-dialog
    title=""
    :visible.sync="previewVisible"
    width="50%"
    >
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog> -->

  </div>
</template>

<script>
export default {
  data () {
    return {
      goodslist: [],
      editFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      var id = parseInt(this.$route.query.id)
      const { data: res } = await this.$http.get(`goods/${id}`)
      console.log(res)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.goodslist = res.data
    }

  },
  computed: {

  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.previewImg {
  width: 100%;
}

.btnAdd {
  margin-top: 15px;
}

</style>
