<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!-- gutter col  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <!-- @clear   -->
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>

      <!-- table -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column width="95px" prop="goods_price" label="()"></el-table-column>
        <el-table-column width="70px" prop="goods_weight" label=""></el-table-column>
        <el-table-column width="140px" prop="add_time" label="">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
              <el-button type="primary" size="mini" icon="el-icon-edit" @click="goEditgoods(scope.row.goods_id)"></el-button>
              <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background>
      </el-pagination>

    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      // 
      goodslist: [],
      // 
      total: 0
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.goodslist = res.data.goods
      this.total = res.data.total
      console.log(res.data)
    },

    // 
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },

    // 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },

    // id 
    async removeById (id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message('')
      }

      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getGoodsList()
    },

    // 
    goAddpage () {
      // goods/add
      this.$router.push('/goods/add')
    },

    // 
    goEditgoods (id) {
      // goods/add
      this.$router.push({
        path: '/goods/edit',
        query: {
          id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>

</style>
