<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
        >
        <!--  -->
        <el-form :model="roleForm" :rules="addRoleRules" ref="addRoleFormRef" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="roleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addrole"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
        >
        <!--  -->
        <el-form :model="editRoleForm" :rules="addRoleRules" ref="editRoleFormRef" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="editRoleForm.roleName" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editRoleInfo"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-table border stripe :data="rolelist">
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for  -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag closable @close="removeRightById(scope.row, item3.id)" type="warning" v-for="(item3) in item2.children" :key="item3.id">
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <!--  -->
            <el-button  type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
            <!--  -->
            <el-button @click="removeRoleById(scope.row.id)"  type="danger" size="mini" icon="el-icon-delete"></el-button>
            <!--  -->
            <el-tooltip  :enterable="false" effect="dark" content="" placement="top">
              <el-button type="warning" size="mini" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
      >
      <!--  -->
      <el-tree ref="treeRef" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" :data="rightslist" :props="treeProps"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rolelist: [],
      // 
      addDialogVisible: false,
      // 
      roleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addRoleRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: '2-10', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, message: '2', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editRoleForm: {},
      // 
      setRightDialogVisible: false,
      // 
      rightslist: [],
      // 
      treeProps: {
        // 
        children: 'children',
        // 
        label: 'authName'
      },
      // 
      defKeys: [],
      // id
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.rolelist = res.data
    },

    // addDialogClosed 
    addDialogClosed () {
      this.$refs.addRoleFormRef.resetFields()
    },

    //  
    addrole () {
      // 
      this.$refs.addRoleFormRef.validate(async vaild => {
        if (!vaild) return
        //  
        const { data: res } = await this.$http.post('roles', this.roleForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        // 
        this.addDialogVisible = false
        // 
        this.getRolesList()
      })
    },

    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // editRoleForm
      this.editRoleForm = res.data
      // 
      this.editDialogVisible = true
    },

    //  
    editDialogClosed () {
      this.$refs.editRoleFormRef.resetFields()
    },

    // 
    editRoleInfo () {
      this.$refs.editRoleFormRef.validate(async vaild => {
        if (!vaild) return
        const { data: res } = await this.$http.put('roles/' + this.editRoleForm.roleId, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.editDialogVisible = false
        // 
        this.getRolesList()
      })
    },

    // 
    async removeRoleById (id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })

      if (confirmResult !== 'confirm') {
        return this.$message('')
      }

      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
    },

    // id
    async removeRightById (role, rightId) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message('!')
      }

      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }

      role.children = res.data
    },

    // 
    async showSetRightDialog (role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      //  data
      this.rightslist = res.data

      // ID
      this.getLeafKeys(role, this.defKeys)
      console.log(this.defKeys)

      this.setRightDialogVisible = true
    },

    //  id, defKeys 
    getLeafKeys (node, arr) {
      // nodechildren() 
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },

    //  
    setRightDialogClosed () {
      this.defKeys = []
    },

    //
    async allotRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]

      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag{
  margin: 10px;
}
.bdtop{
  border-top: 1px solid #eee;
}
.bdbottom{
  border-bottom:1px solid #eee;
}
.vcenter{
  display: flex;
  align-items: center;
}
</style>
