<template>
  <div>

    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

  <!--  -->
  <el-card>
    <el-table border :data="rightsList" stripe>
      <el-table-column label="#" type="index"> </el-table-column>
      <el-table-column label="" prop="authName"> </el-table-column>
      <el-table-column label="" prop="path"> </el-table-column>
      <el-table-column label="" prop="level">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.level === '0'"></el-tag>
          <el-tag v-if="scope.row.level === '1'" type="success"></el-tag>
          <el-tag v-if="scope.row.level === '2'" type="warning"></el-tag>
        </template>
      </el-table-column>
    </el-table>
  </el-card>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rightsList: []
    }
  },
  created () {
    // 
    this.getRightsList()
  },
  methods: {
    // 
    async getRightsList () {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightsList = res.data
    }
  }
}
</script>

